.prevCard {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 700px;
}

.Card {
    height: 400px;
    display: flex;
    justify-content: center;
    background-color: hsl(0, 0%, 100%);
    margin: auto;
    border-radius: 10px;
    box-shadow: 0px 2px 5px 1px hsl(215, 12%, 80%);
}

.ImageProductDesktop {
    border-radius: 10px 0 0 10px;
    height: 400px;
    display: inline;
}

.ImageProductMobile {
    border-radius: 10px 10px 0 0;
    display: none;
}

.Content {
    overflow:hidden;
    float: left;
    width: 210px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 30px;
}

.Price {
    display: flex;
}

.Content p, h1 {
    margin: 0;
    text-align: left;
    display: block;
    float: left;
}

button {
    width: 100%;
    padding: 13px;
    padding-top: 10px;
    background-color: hsl(158, 36%, 37%);
    color: hsl(0, 0%, 100%);
    border: none;
    border-radius: 10px;
    font-family: 'Montserrat';
}

button:active, button:hover {
    background-color: hsl(157, 35%, 16%);
    cursor: pointer;
}

.Perfume {
    font-family: 'Montserrat';
    font-weight: 500;
    font-size: 12px;
    letter-spacing: 5px;
    color: hsl(228, 12%, 48%);
}

.Name {
    font-family: 'Fraunces';
    font-weight: 700;
}

.Description {
    color: hsl(228, 12%, 48%);
    font-family: 'Montserrat';
    font-weight: 500;
    font-size: 12px;
}

.LargePrice {
    color: hsl(158, 36%, 37%);
    font-family: 'Fraunces';
    font-weight: 700;
}

.SmallPrice {
    color: hsl(228, 12%, 48%);
    text-decoration: line-through;
    padding: 8px;
    padding-left: 20px;
    font-size: 12px;
}

@media (max-width: 376px) {
    .ImageProductDesktop {
        display: none;
    }
    .ImageProductMobile {
        display: inline;
        width: 100%;
    }
    .Card {
        flex-direction: column;
        justify-content: start;
        width: 90%;
        height: 580px;
    }
    .Container {
        height: 667px;
    }
    .Content {
        height: 320px;
        overflow:hidden;
        float: left;
        width: 272px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 30px;
    }
    .Description {
        font-size: 13px;
    }
    .prevCard {
        height: 654px;
    }
}
