.PreviewProduct {
    height: 100vh;
    background-color: hsl(30, 38%, 92%);
    margin: 0;
    text-align: center;
    bottom: 0;
}

.attribution {
    font-size: 11px;
    text-align: center;
}

.attribution a, a:active {
    text-decoration: none;
    color: hsl(158, 36%, 37%);
}

.attribution a:active, a:hover {
    color: hsl(212, 21%, 14%);
    text-decoration: underline;
}